<template>
  <div class="article-view">
    <van-nav-bar title="文章" left-text="返回" left-arrow right-text="分享" @click-left="onClickLeft" @click-right="onClickRight"/>
        <div class="ql-container ql-snow">
      <div class="ql-editor">
         <!-- <div class="article-header"> -->
          <h1 class="article-title">{{articleTitle}}</h1>
          <!-- <div class="article-info">
            <span>来自: {{articleData.author}}</span>
            <span>{{articleData.createTime}}</span>
          </div>
        </div> -->
        <!-- <img v-if="this.$route.query.type=='gameReview'&&articleData.showPic!=0&&articleData.originalUrl" :src="articleData.originalUrl" alt="图文图片"> -->
        <div class="article-content">
           <!-- <articleContent></articleContent> -->
           <component :is="articleContent"></component>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import honeyWorth from "../temp/honeyWorth.vue";
import honeyReal from "../temp/honeyReal.vue";
import { NavBar } from "vant";
export default {
  components: {
    [NavBar.name]: NavBar,
    honeyWorth,
    honeyReal
  },
  computed: {
    articleContent() {
      if (this.$route.query.articleId == 1) {
        return "honeyWorth";
      } else if (this.$route.query.articleId == 2) {
        return "honeyReal";
      }
    },
    articleTitle() {
      if (this.$route.query.articleId == 1) {
        return "蜂蜜的功效和作用";
      } else if (this.$route.query.articleId == 2) {
        return "蜂蜜优劣的鉴别";
      }
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
    onClickRight() {}
  }
};
</script>
<style>
.article-view {
  width: 100%;
}
.article-view .ql-container {
  height: calc(100% - 50px);
  overflow: auto;
}
.article-view .article-content {
  font-size: 16px;
  line-height: 30px;
}
</style>



